
@media screen and (min-width:900px){
body{
  background-image: url("snd/404.png");
  background-color: white;
}
}

.bt{
  background-color: tomato;
  visibility: hidden;
  width: 0px;
  
  
  
}

@media screen and (max-width:800px ){
  body{
    background-color: whitesmoke;
  }
  
.bt{
  visibility: visible;
  border: none;
  background-color: whitesmoke;
  width: 100px;
  height: 150px;
  border: none;
  cursor: not-allowed;
  
  
} 
.bt:after{
border: none;
}
.bt:active{
  border: none;
  cursor: not-allowed;
}
  
}